.rightbarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.rightbarProfileImgContainer{
    margin-right: 10px;
    position: relative;
}
.rightbarProfileImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
object-fit: cover;
}
.rightbarUsename{
font-weight: 600;
}
.rightbarOnline{
    width: 12px;
    height: 12px;
    background: limegreen;
    border-radius: 50%;
    position: absolute;
    top: -2px;
    right: 0;
    border: 2px solid #fff;
}